.cmn-toggle-switch {
  position: absolute;
  z-index: 100 !important;
  color: white;
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 108px;
  height: 96px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: none;
  border: none;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  transition: background 0.3s; }

.cmn-toggle-switch:focus {
  outline: none; }

.cmn-toggle-switch-white span {
  background: black !important; }
  .cmn-toggle-switch-white span::before {
    background-color: black !important; }
  .cmn-toggle-switch-white span::after {
    background-color: black !important; }

.active > span::before {
  background-color: white !important; }

.active > span::after {
  background-color: white !important; }

.cmn-toggle-switch span {
  display: block;
  position: absolute;
  top: 45px;
  left: 50px;
  right: 18px;
  height: 3px;
  background: white;
  border-radius: 3px;
  z-index: 51; }

.cmn-toggle-switch span::before,
.cmn-toggle-switch span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  content: "";
  border-radius: 3px; }

.cmn-toggle-switch span::before {
  top: -12px;
  width: 85%; }

.cmn-toggle-switch span::after {
  bottom: -12px;
  width: 60%; }

/**
* Стиль 2
*
* «Гамбургер» превращается в «x» (класс htx).
* Берется исходное состояние, затем полоски плавно перемещаются в центр
* и трансформируются в «x».
*/
.cmn-toggle-switch__htx {
  position: fixed;
  background-color: transparent;
  top: 50%; }
  @media screen and (max-width: 480px) {
    .cmn-toggle-switch__htx {
      top: -1%;
      left: -7%; } }

.cmn-toggle-switch__htx span {
  -webkit-transition: background 0 0.3s;
  transition: background 0 0.3s; }

.cmn-toggle-switch__htx span::before,
.cmn-toggle-switch__htx span::after {
  -webkit-transition-duration: 0.3s, 0.3s;
  transition-duration: 0.3s, 0.3s;
  -webkit-transition-delay: 0.3s, 0;
  transition-delay: 0.3s, 0; }

.cmn-toggle-switch__htx span::before {
  -webkit-transition-property: top, -webkit-transform;
  transition-property: top, transform; }

.cmn-toggle-switch__htx span::after {
  -webkit-transition-property: bottom, -webkit-transform;
  transition-property: bottom, transform; }

/* активное состояние, т.е. меню открыто */
.cmn-toggle-switch__htx.active {
  background-color: transparent; }

.cmn-toggle-switch__htx.active span {
  background: none !important; }

.cmn-toggle-switch__htx.active span::before {
  top: 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

.cmn-toggle-switch__htx.active span::after {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg); }

.cmn-toggle-switch__htx.active span::before,
.cmn-toggle-switch__htx.active span::after {
  -webkit-transition-delay: 0, 0.3s;
  transition-delay: 0, 0.3s;
  width: 110%; }

